"use client";

import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
} from "@/components/ui/card";
import { DollarSign, Film, Users } from "lucide-react";
import { ArrowUp, ArrowDown } from "lucide-react";

// Simple placeholder stats for demonstration
// Replace these values with real data fetched from your API or context
const totalUsers = 1245;
const totalMovies = 320;
const monthlyRevenue = 12750;
const revenueDelta = 14; // % increase/decrease from last month

export default function SidePanelOverview() {
  return (
    <section className="space-y-4">
      <Card className="shadow-md">
        <CardHeader className="pb-2">
          <CardTitle className="text-sm font-medium">Overview</CardTitle>
          <CardDescription className="text-xs text-muted-foreground">
            Quick stats and recent activity
          </CardDescription>
        </CardHeader>
        <CardContent className="grid gap-4">
          {/* Stats Row */}
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            {/* Total Users */}
            <div className="flex items-center p-3 rounded-md bg-muted">
              <div className="p-2 rounded-md bg-primary/10 text-primary mr-3">
                <Users className="w-4 h-4" />
              </div>
              <div className="space-y-0.5">
                <p className="text-xs text-muted-foreground leading-tight">
                  Total Users
                </p>
                <p className="text-sm font-semibold">{totalUsers}</p>
              </div>
            </div>

            {/* Total Movies */}
            <div className="flex items-center p-3 rounded-md bg-muted">
              <div className="p-2 rounded-md bg-primary/10 text-primary mr-3">
                <Film className="w-4 h-4" />
              </div>
              <div className="space-y-0.5">
                <p className="text-xs text-muted-foreground leading-tight">
                  Total Movies
                </p>
                <p className="text-sm font-semibold">{totalMovies}</p>
              </div>
            </div>
          </div>

          {/* Revenue Info */}
          <div className="flex items-center justify-between p-3 rounded-md bg-muted">
            <div className="flex items-center gap-3">
              <div className="p-2 rounded-md bg-primary/10 text-primary">
                <DollarSign className="w-4 h-4" />
              </div>
              <div className="space-y-0.5">
                <p className="text-xs text-muted-foreground leading-tight">
                  Monthly Revenue
                </p>
                <p className="text-sm font-semibold">
                  ${monthlyRevenue.toLocaleString()}
                </p>
              </div>
            </div>
            <div
              className={`flex items-center gap-1 text-xs font-medium ${
                revenueDelta >= 0 ? "text-green-600" : "text-red-600"
              }`}
            >
              {revenueDelta >= 0 ? (
                <ArrowUp className="w-4 h-4" />
              ) : (
                <ArrowDown className="w-4 h-4" />
              )}
              <span>{Math.abs(revenueDelta)}%</span>
            </div>
          </div>

          {/* Placeholder Chart or Activity */}
          <div className="mt-2">
            <div className="w-full h-24 bg-muted rounded-md flex items-center justify-center">
              <span className="text-sm text-muted-foreground">
                [ Chart Placeholder ]
              </span>
            </div>
          </div>
        </CardContent>
      </Card>
    </section>
  );
}
